import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CommonService } from 'app/@theme/services/common.service';
import { OrderService } from 'app/@theme/services/order.service';

@Component({
  selector: 'ngx-new-order',
  templateUrl: './new-order.component.html',
  styleUrls: ['./new-order.component.scss']
})
export class NewOrderComponent implements OnInit {
  newOrderList :any = [];
  isAdmin:boolean = false;
  constructor(private orderService: OrderService, private commonService: CommonService) {
    this.commonService.setTitle("Recent Orders")
   }

  ngOnInit(): void {
    this.checkIsAdmin();
  }

  checkIsAdmin(){
    if(this.commonService.getUser().name == 'ADMIN'){
      this.isAdmin = true;
      this.getAllNewOrderList();
    }
      
    else
      this.isAdmin = false;
  }

  getAllNewOrderList(){
    this.orderService.getNewOrders().subscribe(
      data=>{
        if(data['success']){
          this.newOrderList = data['data'];
        }
        else
          this.newOrderList = [];
      },error=>{
        this.newOrderList = [];
      }
    )
  }

}
